/*===============================
=            Choices            =
===============================*/
.choices {
  position: relative;
  margin-bottom: 1rem;
  font-size: 1rem; }
  .choices:focus {
    outline: none; }
  .choices:last-child {
    margin-bottom: 0; }
  .choices.is-disabled .choices__inner, .choices.is-disabled .choices__input {
    background-color: #dee2e6;
    cursor: not-allowed;
    user-select: none; }
  .choices.is-disabled .choices__item {
    cursor: not-allowed; }

.choices[data-type*="select-one"] {
  cursor: pointer; }
  .choices[data-type*="select-one"] .choices__inner {
    padding-bottom: 7.5px; }
  .choices[data-type*="select-one"] .choices__input {
    display: block;
    width: 100%;
    padding: 10px;
    border-bottom: 1px solid #868e96;
    background-color: #FFFFFF;
    margin: 0; }
  .choices[data-type*="select-one"] .choices__button {
    background-image: url("../../icons//cross-inverse.svg");
    padding: 0;
    background-size: 8px;
    height: 100%;
    position: absolute;
    top: 50%;
    right: 0;
    margin-top: -10px;
    margin-right: 25px;
    height: 20px;
    width: 20px;
    border-radius: 10em;
    opacity: .5; }
    .choices[data-type*="select-one"] .choices__button:hover, .choices[data-type*="select-one"] .choices__button:focus {
      opacity: 1; }
    .choices[data-type*="select-one"] .choices__button:focus {
      box-shadow: 0px 0px 0px 2px #228ae6; }
  .choices[data-type*="select-one"]:after {
    content: "";
    height: 0;
    width: 0;
    border-style: solid;
    border-color: #333 transparent transparent transparent;
    border-width: 5px;
    position: absolute;
    right: 11.5px;
    top: 50%;
    margin-top: -2.5px;
    pointer-events: none; }
  .choices[data-type*="select-one"].is-open:after {
    border-color: transparent transparent #333 transparent;
    margin-top: -7.5px; }
  .choices[data-type*="select-one"][dir="rtl"]:after {
    left: 11.5px;
    right: auto; }
  .choices[data-type*="select-one"][dir="rtl"] .choices__button {
    right: auto;
    left: 0;
    margin-left: 25px;
    margin-right: 0; }

.choices[data-type*="select-multiple"] .choices__inner, .choices[data-type*="text"] .choices__inner {
  cursor: text; }

.choices[data-type*="select-multiple"] .choices__button, .choices[data-type*="text"] .choices__button {
  position: relative;
  display: inline-block;
  margin-top: 0;
  margin-right: -0.25rem;
  margin-bottom: 0;
  margin-left: 0.5rem;
  padding-left: 1rem;
  border-left: 1px solid #1670bf;
  background-image: url("../../icons//cross.svg");
  background-size: 0.5rem;
  width: 0.5rem;
  line-height: 1;
  opacity: .75; }
  .choices[data-type*="select-multiple"] .choices__button:hover, .choices[data-type*="select-multiple"] .choices__button:focus, .choices[data-type*="text"] .choices__button:hover, .choices[data-type*="text"] .choices__button:focus {
    opacity: 1; }

.choices__inner {
  display: inline-block;
  vertical-align: top;
  width: 100%;
  background-color: #fff;
  padding: 7.5px 7.5px 3.75px;
  border: 1px solid #868e96;
  border-radius: 0.5rem;
  font-size: 1rem;
  min-height: 44px;
  overflow: hidden; }
  .is-focused .choices__inner, .is-open .choices__inner {
    border-color: #60686f; }
  .is-open .choices__inner {
    border-radius: 0.5rem 0.5rem 0 0; }
  .is-flipped.is-open .choices__inner {
    border-radius: 0 0 0.5rem 0.5rem; }

.choices__list {
  margin: 0;
  padding-left: 0;
  list-style: none; }

.choices__list--single {
  display: inline-block;
  padding: 4px 16px 4px 4px;
  width: 100%; }
  [dir="rtl"] .choices__list--single {
    padding-right: 4px;
    padding-left: 16px; }
  .choices__list--single .choices__item {
    width: 100%; }

.choices__list--multiple {
  display: inline; }
  .choices__list--multiple .choices__item {
    display: inline-block;
    vertical-align: middle;
    border-radius: 0.5rem;
    padding: 4px 10px;
    font-size: 0.875rem;
    font-weight: 500;
    margin-right: 3.75px;
    margin-bottom: 3.75px;
    background-color: #228ae6;
    border: 1px solid #187dd6;
    color: #FFFFFF;
    word-break: break-all; }
    .choices__list--multiple .choices__item[data-deletable] {
      padding-right: 5px; }
    [dir="rtl"] .choices__list--multiple .choices__item {
      margin-right: 0;
      margin-left: 3.75px; }
    .choices__list--multiple .choices__item.is-highlighted {
      background-color: #187dd6;
      border: 1px solid #1670bf; }
    .is-disabled .choices__list--multiple .choices__item {
      background-color: #95a2af;
      border: 1px solid #788999; }

.choices__list--dropdown {
  display: none;
  z-index: 1;
  position: absolute;
  width: 100%;
  background-color: #fff;
  border: 1px solid #868e96;
  top: 100%;
  margin-top: -1px;
  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
  overflow: hidden;
  word-break: break-all; }
  .choices__list--dropdown.is-active {
    display: block; }
  .is-open .choices__list--dropdown {
    border-color: #60686f; }
  .is-flipped .choices__list--dropdown {
    top: auto;
    bottom: 100%;
    margin-top: 0;
    margin-bottom: -1px;
    border-radius: .25rem .25rem 0 0; }
  .choices__list--dropdown .choices__list {
    position: relative;
    max-height: 300px;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    will-change: scroll-position; }
  .choices__list--dropdown .choices__item {
    position: relative;
    padding: 10px;
    font-size: 1rem; }
    [dir="rtl"] .choices__list--dropdown .choices__item {
      text-align: right; }
  @media (min-width: 640px) {
    .choices__list--dropdown .choices__item--selectable {
      padding-right: 100px; }
      .choices__list--dropdown .choices__item--selectable:after {
        content: attr(data-select-text);
        font-size: 0.875rem;
        opacity: 0;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%); }
      [dir="rtl"] .choices__list--dropdown .choices__item--selectable {
        text-align: right;
        padding-left: 100px;
        padding-right: 10px; }
        [dir="rtl"] .choices__list--dropdown .choices__item--selectable:after {
          right: auto;
          left: 10px; } }
  .choices__list--dropdown .choices__item--selectable.is-highlighted {
    background-color: #f2f2f2; }
    .choices__list--dropdown .choices__item--selectable.is-highlighted:after {
      opacity: .5; }

.choices__item {
  cursor: default; }

.choices__item--selectable {
  cursor: pointer; }

.choices__item--disabled {
  cursor: not-allowed;
  user-select: none;
  opacity: .5; }

.choices__heading {
  font-weight: 600;
  font-size: 0.875rem;
  padding: 10px;
  border-bottom: 1px solid #a1a8ae;
  color: gray; }

.choices__button {
  text-indent: -9999px;
  -webkit-appearance: none;
  appearance: none;
  border: 0;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer; }
  .choices__button:focus {
    outline: none; }

.choices__input {
  display: inline-block;
  vertical-align: baseline;
  background-color: #f9f9f9;
  font-size: 1rem;
  margin-bottom: 5px;
  border: 0;
  border-radius: 0;
  max-width: 100%;
  padding: 4px 0 4px 2px; }
  .choices__input:focus {
    outline: 0; }
  [dir="rtl"] .choices__input {
    padding-right: 2px;
    padding-left: 0; }

.choices__placeholder {
  opacity: .5; }

/*=====  End of Choices  ======*/
.form-group {
  padding-top: 1.5rem; }

.form-error ~ .choices .choices__inner {
  border-color: #fa5252; }

.choices[data-type*="select-one"] .choices__inner {
  padding-top: 0;
  padding-bottom: 0; }

.choices[data-type*="select-one"] .choices__input {
  padding: .5rem .75rem;
  border-bottom: 2px solid #868e96; }

.choices[data-type*="select-multiple"] .choices__button, .choices[data-type*="text"] .choices__button {
  border-left: 2px solid #1670bf; }

.choices__inner {
  min-height: 0;
  padding: .125rem .75rem 0;
  border: 2px solid #868e96;
  font-family: "freight-sans-pro", "FreightSans Pro", "PT Sans", Arial, sans-serif; }
  .is-focused .choices__inner,
  .is-open .choices__inner {
    border-color: #228ae6; }

.choices__list--single {
  padding: 0 16px 0 0; }

.choices__list--multiple .choices__item {
  margin-right: .25rem;
  margin-bottom: .25rem;
  padding: 0 .5rem;
  border: 2px solid #dee2e6;
  background-color: #f1f3f5;
  color: #212529; }
  .choices__list--multiple .choices__item.is-highlighted {
    border: 2px solid #228ae6;
    background-color: #f1f3f5; }
  .is-disabled .choices__list--multiple .choices__item {
    border: 2px solid #788999; }

.choices__list--dropdown {
  margin-top: -2px;
  border: 2px solid #868e96; }
  .is-open .choices__list--dropdown {
    border-color: #228ae6; }
  .is-flipped .choices__list--dropdown {
    margin-bottom: -2px; }
  .choices__list--dropdown .choices__item {
    padding: .25rem .75rem; }
  @media (min-width: 640px) {
    .choices__list--dropdown .choices__item--selectable {
      padding-right: 6rem; }
      .choices__list--dropdown .choices__item--selectable::after {
        right: .5rem; } }

.choices__heading {
  padding: .5rem;
  border-bottom: 2px solid #a1a8ae; }

.choices__input {
  background-color: #fff;
  font-family: "freight-sans-pro", "FreightSans Pro", "PT Sans", Arial, sans-serif; }

.is-focused ~ .form-label,
.is-open ~ .form-label {
  color: #228ae6; }
